<template>
  <div class="classify">
    <header>
      <van-search shape="round" placeholder="请输入搜索关键词"/>
    </header>
    <div class="classifybox clearfix">
      <aside class="fl">
         <!-- <van-sidebar-item title="标签名称" /> -->
        <van-sidebar v-model="activeIndex">
          <van-sidebar-item v-for="(item, index) in types" :key="index"  
          :id="item.proprotypeId"
          :title="item.protypeTitle"
          @click="toggleType(item, index)"/>
        </van-sidebar>
      </aside>

      <div class="content fl">
        <van-row>
          <van-col span="8" class="item" v-for="(item,index) in curTypeProData" :key="index" @click="previewProDetail(item.pid)">
            <div>
              <img class="autoimg" :src="imgBaseUrl + item.pimg" alt />
            </div>
            <div class="one-txt name">{{item.pname}}</div>
          </van-col>
          <!-- <van-col span="8" class="item">
            <div>
              <img class="autoimg" src="http://img0.imgtn.bdimg.com/it/u=3024308660,1355431406&fm=26&gp=0.jpg" alt="">
            </div>
            <div class="one-txt name">11</div>
          </van-col> -->
        </van-row>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'classify',
      data() {
      return {
        activeIndex: 0, //激活的类型下标
        types:[], //所有商品类型
        curTypeProData:[],  //当前类型的商品
        imgBaseUrl:'http://127.0.0.1:10008/assets/', //图片接口
        isHas: false //存在缓存数据 
      } 
    },

    created(){
      // 切换获取数据 - 本地缓存数据let cacheTypeProData = {}; -  //初始化缓存cacheTypeProData[v.protypeId] = [];-//将缓存数据结构保存在本地存储 _ 方法中序列化 —— 判断
      
      //获取本地缓存数据  cache快取的意思
      let cacheTypeProData = localStorage.getItem('cacheTypeProData');

      if (!cacheTypeProData) {
        cacheTypeProData = {}; //初始化缓存数据结构
        // console.log("不存在缓存数据");
      } else {
        // console.log('存在缓存数据');
        cacheTypeProData = JSON.parse(cacheTypeProData);
        this.isHas = true;
      }

      //发起请求
      this.axios({
        method:'GET',
        url:'/protype', //商品类型请求
      })
      .then(result =>{
        // console.log('classify result=>',result);
        if(result.data.status === 4000){
           result.data.data.forEach((v, i) => {
              v.isSelect = i == 0; //添加属性

              //初始化缓存
              if(!this.isHas){
                cacheTypeProData[v.protypeId] = [];
              }
           });

          //将缓存数据结构保存在本地存储
          if(!this.isHas){
            localStorage.setItem('cacheTypeProData',JSON.stringify(cacheTypeProData));
          }

          this.types = result.data.data.concat();
          // console.log(this.types[0].protypeId);

          this.getProOfType(this.types[0].protypeId);
        }
        // console.log('this.types=>',this.types);
        // console.log('cacheTypeProData=>',cacheTypeProData);

      }).catch(err =>{
        // console.log('err=>','出错！');
      })
    },

    methods:{
      //根据商品类型查看商品
      getProOfType(protypeId){
        //进行数据缓存
        let cacheTypeProData = JSON.parse(localStorage.getItem('cacheTypeProData'));
        
        //判断是否存在缓存数据 (Yes就在本地获取数据,No就发起请求)
        if(cacheTypeProData[protypeId].length > 0){
          // console.log('不发起请求，在本地存储获取数据');
          this.curTypeProData = cacheTypeProData[protypeId].concat();
          return;
        }

        // console.log('发起请求，从服务器获取数据');

        this.axios({
          method:'GET',
          url:'/apptypePro',
          params:{
            protypeId  //protypeId: 商品类型id
          }
        }).then(result =>{
          // console.log('getProOfType result ==> ', result);
          if (result.data.status === 4000) {
            this.curTypeProData = result.data.data.rows.concat();

            // let cacheTypeProData = JSON.params(localStorage.getItem('cacheTypeProData'));
            cacheTypeProData[protypeId].push(...result.data.data.rows);

            localStorage.setItem(
              'cacheTypeProData',
              JSON.stringify(cacheTypeProData)
            );
          }

        }).catch(err =>{
          // console.log('getProOfType err=>','出错！');
        })

      },

      //切换类型
      toggleType(item, index) {
        if (item.isSelect) {
          return;
        }
        this.types[this.activeIndex].isSelect = false;

        this.activeIndex = index;

        item.isSelect = true;

        this.getProOfType(item.protypeId);
      },

      // 点击跳转预览商品详情 (通过id辨识具体商品)
      previewProDetail(id){
        this.$router.push({name:'detail',params:{id}});
      }

    }
  }
</script>

<style lang="less" scoped>
  .classify{
    .classifybox{
      position: fixed;
      top: 54px;
      bottom: 50px;
      left: 0;
      right: 0;
    }
    aside {
      width: 100px;
      height: 100%;
      background-color: #FAFAFA;
      /deep/.van-sidebar-item{
        padding: 0;
        height: 40px;
        line-height: 40px;
        text-align: center;
        font-size: 16px;
      }
      .van-sidebar-item--select{
        width: 100%;
      }
      .van-sidebar {
          width:100%;
      }
    }
    .content{
      width: calc(~"100% - 110px");
      height: calc(~"100% - 20px");
      background-color:#fff;
      padding: 10px 5px;
      overflow-y: auto;
      .item {
        padding: 0 4px;
        margin-bottom: 15px;
        .name {
          margin-top: 4px;
          text-align: center;
          font-weight: bold;
        }
      }
    }
  }
</style>